import React from "react";
import './NameCard.css'

const nameCheepURL = "https://www.namecheap.com/domains/registration/results/?domain=";

const NameCard = ({ suggestedName }) => {
    return (
        <a
            target="_blank"
            rel="noreferrer"
            className="card-link"
            href= {`${nameCheepURL}${suggestedName}`}
        >
            <div className="result-name-card">
                <p className="result-name">
                    {suggestedName}
                </p>
            </div>
        </a>
    )
}

export default NameCard;